import { Component, Input, Output, EventEmitter } from '@angular/core';
import { faPlus, faMinus } from '@fortawesome/free-solid-svg-icons';
@Component({
  selector: 'lib-email',
  templateUrl: './email.component.html',
  styleUrls: ['./email.component.css']
})
export class EmailComponent {
  faPlus: any = faPlus;
  faMinus: any = faMinus;

  @Input() maxlength: Number = 50;
  @Input() emailFormateError: String = 'Email is not valid';
  @Input() duplicateEmailError: String = 'Do not enter same email';
  @Input() name: String = '';
  @Input() emailArray: any;
  @Input() id: String = '';
  @Input() disabled: Boolean = false;
  @Input() value: String = '';
  @Input() isMultiple: Boolean = true;
  @Input() placeholder: String = 'Email';
  @Input() isShowPlus: Boolean = false;
  @Input() tabindex: Number = 0;
  @Input() index: Number = 0;
  @Output() focus: EventEmitter<any> = new EventEmitter<any>();
  @Output() blur: EventEmitter<any> = new EventEmitter<any>();
  @Output() change: EventEmitter<any> = new EventEmitter<any>();
  @Output() onPlus: EventEmitter<any> = new EventEmitter<any>();

  onChange(event, index) {
    let data = { event: event, index: index };
    this.change.emit(data);
  }

  isError: Boolean = false;
  isUniqueError: Boolean = false;

  onBlur(event, id, emailArray) {
    debugger;
    if (event.target.value !== '') {
      const validEmailRegex = /[A-Z0-9a-z._%+-]+@[A-Za-z0-9.-]+\.[A-Za-z]{2,}/;
      if (!validEmailRegex.test(event.target.value)) {
        this.isError = true;
      } else {
        this.isError = false;
        emailArray.map((item, index) => {
          if (index !== id) {
            if (item.email === event.target.value) {
              this.isUniqueError = true;
            } else {
              this.isUniqueError = false;
            }
          }
        });
      }
    }
    this.blur.emit(event);
  }
  onFocus(event, index) {
    this.focus.emit(event);
  }
  onPlusClick() {
    this.onPlus.emit();
  }
}
